.modal-box {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    min-width: 200px;
    width: 45vw;
    background-color: #fff5eb;
    box-shadow: 24px 24px 48px rgba(0, 0, 0, 0.1);
    padding: 16px 32px 24px 32px;
    border-radius: 12px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 12px;
    text-align: center;
}

.modal-icon {
    font-size: 15vw !important;
    color: #295435;
}

.modal-button {
    width: 180px;
    min-width: 100px;
    height: 35px;
    color: #fff;
    border: none;
    font-weight: 600;
    border-radius: 8px;
    cursor: pointer;
}

.check-button {
    background-color: #227e3c;
}